// 军训人数
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';

// let jsonData = {
//   "axis":["心理健康学生", "心理咨询学生","参与心理健康活动学生","选择心理健康课程学生"],
//   "data":[8060, 7265, 9232, 6823]
// }

export default class MentalHealth extends Component {
  state={
    axis: [],
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/student/sxpd/queryStudentMentalHealth').then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          axis: res.obj.axis,
          data: res.obj.data,
        })
      }
    })
  }
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: this.state.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      color:['#F5A623'],
      yAxis: {
        name:'%',
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show: false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          data: this.state.data,
          label: {
            normal: {
              color:'#fff',
              show: true,
              position: 'top'
            }
          },
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='MentalHealthBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
