// 学生获奖情况
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import LineEcharts from '../../../common/components/BaseEcharts/LineEcharts';
// import jsonData from '../../../data/专业视角--学生获奖情况.json';

export default class ManagementAward extends Component {
  state={
    data: [
      
    ],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/student/sxpd/queryManagerAwardNum').then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          data: res.obj,
        })
      }
    })
  }
  render(){
    let {data} = this.state;
    let option = {
      title: {
        show: false
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        },
        data: data[0] && data[0]['value'].map(item => {
          return item.name
        })
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color:'#00CFFF'
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      color:['#30CCF4','#DED324','#EE6AE6'],
      series: data[0] && data.map(item => {
        return {
          name: item.name,
          type: 'line',
          data: item['value'].map(child =>
              child['value']
          )
        };
      })
    };

    return(
      <Col span={24} style={{height:'100%'}}>
        <LineEcharts
          id='ManagementAwardBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
