// 文明校园
import React, {Component} from 'react';
import {Col} from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';

export default class CivilizedCampus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      classNum:null,
      dormitory:null,
      stuNum:null
    }
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/student/sxpd/queryCultureStudent').then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          classNum: res.obj.classNum,
          dormitory: res.obj.dormitory,
          stuNum: res.obj.stuNum
        })
      }
    })
  }
  render(){
    return(
      <Col span={24} style={{padding:'10px'}}>
        <div>
          <label className={'major_visual_teach_team_label'}>文明班级数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.classNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>文明宿舍数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.dormitory}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>文明学生数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.stuNum}</span>
        </div>
      </Col>
    )
  }
}
