// 思想品德-党员发展
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
export default class MembersDevelopment extends Component {
  constructor(props){
    super(props);
    this.state = {
      membersNum: null,
      intoNum: null,
      welfareNum: null
    }
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/student/sxpd/queryStudentPartyMember').then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          membersNum: res.obj.membersNum,
          intoNum: res.obj.intoNum,
          welfareNum: res.obj.welfareNum
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={24} className={'major_visual_teach_reform_number_large'}>{this.state.membersNum}</Col>
        <Col span={24} className={'major_visual_teach_reform_title'}>学生党员人数</Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.intoNum}</div>
          <div className={'major_visual_teach_reform_title'} style={{width:'140px'}}>入党积极分子人数</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.welfareNum}</div>
          <div className={'major_visual_teach_reform_title'} style={{width:'140px'}}>参加公益活动人数</div>
        </Col>
      </Row>
    )
  }
}
