import React, { Component } from 'react'
import { Carousel} from 'antd'
import MembersDevelopment from './components/MembersDevelopment';
import CivilizedCampus from './components/CivilizedCampus';
import JoinArmy from './components/JoinArmy';
import MilitaryTrainingNum from './components/MilitaryTrainingNum';
import SportsActivities from './components/SportsActivities';
import MentalHealth from './components/MentalHealth';
import ManagementAward from './components/ManagementAward';
import Instructor from './components/Instructor';
import AwardPersonNum from './components/AwardPersonNum';
import AwardNum from './components/AwardNum';
export default class Xuesheng extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>          
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                思想品德-党员发展
                            </span>

              </div>
              <div className='item-chart'>
              <MembersDevelopment />
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                思想品德-文明校园
                            </span>

              </div>
              <div className='item-chart'>
              <CivilizedCampus/>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                思想品德-参军入伍
                            </span>

              </div>
              <div className='item-chart'>
              <JoinArmy/>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                思想品德-军训人数
                            </span>

              </div>
              <div className='item-chart'>
              <MilitaryTrainingNum/>
              </div>              
            </div>
          </div>
             
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                身体发展-体育活动
                            </span>

              </div>
              <div className='item-chart'>
              <SportsActivities/>
              </div>              
            </div>
          </div>
          <div className='item-wrapper-2'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                心理发展-心理健康
                            </span>

              </div>
              <div className='item-chart'>
              <MentalHealth/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                身心发展-管理人员获奖
                            </span>

              </div>
              <div className='item-chart'>
              <ManagementAward/>
              </div>              
            </div>
          </div>  
          <div className='item-wrapper-2'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                身心发展-辅导员
                            </span>

              </div>
              <div className='item-chart'>
              <Instructor/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                学业奖励-获奖人数

                            </span>

              </div>
              <div className='item-chart'>
              <AwardPersonNum/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                学业奖励-获奖数量
                            </span>

              </div>
              <div className='item-chart'>
              <AwardNum/>
              </div>              
            </div>
          </div>   
          
            
        </div>
        {/* 每一页的划分 */}        
        
      </Carousel>
    )
  }
}
