// 辅导员
import React, {Component} from 'react';
import {Col} from 'antd';
import { getAction } from '../../../axios';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
// import jsonData from '../../../data/学生视角-辅导员.json';

export default class Instructor extends Component {
  state={
    axis: [],
    data: [],
  }
  componentDidMount(){
    this.getData();
  }
  getData(){
    getAction('/bigdata/student/sxpd/queryInstructorManage').then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          axis: res.obj.axis,
          data: res.obj.data,
        })
      }
    })
  }
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        data: this.state.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          },
          formatter: function (params, index) {
            var newParamsName = "";
            var splitNumber = 5;
            var paramsNameNumber = params && params.length;
            if (paramsNameNumber && paramsNameNumber <= 4) {
              splitNumber = 4;
            } else if (paramsNameNumber >= 5 && paramsNameNumber <= 7) {
              splitNumber = 4;
            } else if (paramsNameNumber >= 8 && paramsNameNumber <= 9) {
              splitNumber = 5;
            } else if (paramsNameNumber >= 10 && paramsNameNumber <= 14) {
              splitNumber = 5;
            } else {
              params = params && params.slice(0, 15);
            }

            var provideNumber = splitNumber; //一行显示几个字
            var rowNumber = Math.ceil(paramsNameNumber / provideNumber) || 0;
            if (paramsNameNumber > provideNumber) {
              for (var p = 0; p < rowNumber; p++) {
                var tempStr = "";
                var start = p * provideNumber;
                var end = start + provideNumber;
                if (p === rowNumber - 1) {
                  tempStr = params.substring(start, paramsNameNumber);
                } else {
                  tempStr = params.substring(start, end) + "\n";
                }
                newParamsName += tempStr;
              }

            } else {
              newParamsName = params;
            }
            params = newParamsName;
            return params;
          },
        }
      },
      color:['#00CFFF'],
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show: false
        },
        splitLine:{
          show:false
        }
      },
      series: [
        {
          type: 'bar',
          data: this.state.data,
          label: {
            normal: {
              color:'#fff',
              show: true,
              position: 'top'
            }
          },
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='InstructorNumBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
